* {
    margin: 0;
    padding: 0;
}

.subscribe-chocolate-recipes {
    background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.20) 0%, rgba(0, 0, 0, 0.20) 100%), url(../images/subscribe/mobile.jpg);
    height: 370px;
    flex-shrink: 0;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    margin-top: 100px;
}
@media (min-device-pixel-ratio: 2),
(-webkit-min-device-pixel-ratio: 2),
(min-resolution: 192dpi),
(min-resolution: 2dppx) {
    .subscribe-chocolate-recipes {
        background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.20) 0%, rgba(0, 0, 0, 0.20) 100%), url(../images/subscribe/mobilex2.jpg);
    }
}

.subscribe-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 24px;
    padding-left: 20px;
    padding-right: 20px;
}
.subscribe-headline {
    color: var(--white);
    text-align: center;
    font-size: 20px;
    font-weight: 600;
    line-height: 24px;
    letter-spacing: -0.6px;
    text-transform: uppercase;
    padding-top: 62px;
    width: 291px;
}
.subscribe-paragraph {
    color: var(--white);
    text-align: center;
    font-size: 14px;
    font-weight: 400;
    line-height: 18px;
    letter-spacing: -0.28px;
    width: 326px;
}
.subscribe-button {
    color: var(--white);
    background-color: var(--orange);
    font-size: 14px;
    font-weight: 600;
    line-height: normal;
    border-radius: 100px;
    padding: 12px 28px;

}
