/****** MODAL ******/
* {
    margin: 0;
    padding: 0;
}

.subscribe-modal-is-hidden {
    pointer-events: none;
    visibility: hidden;
    opacity: 0;
}

.subscribe-modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #2e2f4266;
    padding: 72px 24px 24px 24px;
    transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
}


.subscribe-modal-window {
    max-width: fit-content;
    margin-left: auto;
    margin-right: auto;
    background-color: var(--background-color);
    border-radius: 15px;
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
    display: flex;
    flex-direction: column;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
}

.subscribe-modal-window>* {
    width: 100%;
}

.subscribe-modal-close-x {
    stroke: var(--white);
    position: absolute;
    top: 15px;
    right: 14px;
    transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.subscribe-modal-close-x:hover,
.subscribe-modal-close-x:focus {
    stroke: var(--orange)
}

.subscribe-modal-window {
    width: 311px;
    height: 350px;
}

.subscribe-modal-hero {
    background: url(../images/subscribe_pop-up/subscribe-chocolate-1x.jpg),
        lightgray 0px -81.483px / 172.99% 212.406%;
    background-repeat: no-repeat;
    background-size: cover;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
    width: 311px;
    height: 169px;
}

.subscribe-modal-close-x {
    stroke: var(--white);
    position: absolute;
    top: 15px;
    right: 14px;
    transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.subscribe-modal-close-x:hover,
.subscribe-modal-close-x:focus {
    stroke: var(--orange)
}

.subscribe-modal-paragraph {
    padding: 32px 24px 0;
}


.subscribe-modal-text-one {
    color: var(--black);
    font-family: inherit;
    font-size: 20px;
    font-style: normal;
    font-weight: 600;
    line-height: 24px;
    letter-spacing: -0.6px;
    text-transform: uppercase;
}

.subscribe-modal-text-two {
    color: var(--orange);
    font-family: inherit;
    font-size: 20px;
    font-style: normal;
    font-weight: 600;
    line-height: 24px;
    letter-spacing: -0.6px;
    text-transform: uppercase;
}

.subscribe-modal-blocks {
    gap: 24px;
    background-color: #FAFAFA;
}

.subscribe-modal-label {
    display: flex;
    width: 263px;
    padding: 14px 18px;
    align-items: flex-start;
    gap: 40px;
}

.subscribe-modal-input {
    display: flex;
    width: 263px;
    padding: 14px 18px;
    align-items: flex-start;
    gap: 40px;

    border-radius: 25px;
    border: 1.5px solid rgba(17, 17, 17, 0.05);
    box-shadow: 0px 5.44492px 5.44492px 0px rgba(0, 0, 0, 0.03);

}


.subscribe-modal-submit {
    position: absolute;
    right: 24px;
    bottom: 40px;

    display: flex;
    width: 100px;
    height: 47px;

    padding: 14px 28px;
    justify-content: center;
    align-items: center;
    gap: 14px;

    border-radius: 100px;
    background: var(--orange);
    color: var(--white);
    text-align: center;
    font-family: inherit;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    transition: background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
}

.subscribe-modal-submit:hover,
.subscribe-modal-submit:focus {
    color: var(--orange);
    background-color: var(--bright-orange);
}


@media (min-width: 768px) {

    .subscribe-modal-window {
        width: 538px;
        height: 412px;
    }

    .subscribe-modal-hero {
        width: 538px;
        height: 196px;
    }

    .subscribe-modal-label {
        width: auto;
        padding: 0;
    }

    .subscribe-modal-paragraph {
        padding: 40px 40px 0;
    }

    .subscribe-modal-input {
        display: flex;
        width: 313px;
        height: 52px;
        padding: 14px 24px;
        align-items: flex-start;
        gap: 40px;
        border-radius: 30px;
        border: 1.5px solid rgba(17, 17, 17, 0.05);
        margin: 40px;
    }

    .subscribe-modal-form {
        display: flex;
        align-items: center;
        gap: 8px;
    }

    .subscribe-modal-submit {
        display: flex;
        width: 137px;
        height: 52px;
        padding: 17px 40px;
        justify-content: center;
        align-items: center;
        gap: 14px;
        border-radius: 100px;
        background: var(--orange);
        margin-right: 40px;
        position: absolute;
        right: 0px;
        bottom: 40px;
    }
}