:root {
  --black: #111;
  --grey: #1e1823;
  --orange: #fd9222;
  --bright-orange: #fcf1e0;
  --white: white;
  --tiny-p: #111111b3;
  --background-color: #fafafa;
  --font: "Montserrat", sans-serif;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: var(--background-color);
  font-family: Montserrat, sans-serif;
}

button {
  cursor: pointer;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.container {
  margin: 0 auto;
}

a {
  cursor: pointer;
  text-decoration: none;
}

li {
  list-style-type: none;
}

* {
  margin: 0;
  padding: 0;
}

.header_networks, .header_nav-list {
  display: none;
}

.header-link {
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.header-link svg {
  transition: stroke .25s cubic-bezier(.4, 0, .2, 1);
}

.header-link:hover {
  color: var(--orange);
}

.header-link svg {
  stroke: var(--black);
}

.header-link svg:hover {
  stroke: var(--orange);
}

button {
  background-color: #0000;
  border: none;
}

.header {
  justify-content: center;
  display: flex;
}

.header-container {
  width: 100%;
  margin: 20px 20px 14px;
}

.header_nav {
  justify-content: space-between;
  display: flex;
}

.header-logo .logospan {
  font-style: normal;
}

.header-logo {
  color: var(--black);
  letter-spacing: -.42px;
  align-items: center;
  gap: 4px;
  font-family: Montserrat;
  font-size: 14px;
  font-style: italic;
  font-weight: 700;
  line-height: 18px;
  display: flex;
}

.menu-container {
  background-color: var(--grey);
  z-index: 999;
  flex-direction: column;
  justify-content: space-between;
  width: 220px;
  height: 100vh;
  padding: 20px;
  transition: transform .25s ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(100%);
}

.menu-container.is-open {
  transform: translateX(0);
}

.menu-container .menu-toggle {
  color: var(--white);
  position: absolute;
  top: 16px;
  right: 16px;
}

.menu-toggle svg {
  stroke: var(--white);
}

.menu-toggle svg:hover {
  stroke: var(--orange);
}

.mobile-menu {
  flex-direction: column;
  gap: 24px;
  margin-top: 136px;
  margin-left: 30px;
  display: flex;
}

.mobile-menu .header-link {
  color: var(--white);
  letter-spacing: -.28px;
  text-align: left;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 16px;
}

.mobile-menu .header-link:hover, .mobile-menu .active {
  color: var(--orange);
}

.header_networks-mobile {
  gap: 18px;
  display: flex;
}

@media (width >= 768px) and (width <= 1199px) {
  .header-container {
    margin: 24px 32px;
  }

  button svg {
    width: 32px;
    height: 32px;
  }

  .menu-container {
    width: 290px;
    padding: 24px 32px;
  }

  .mobile-menu {
    margin-left: 53px;
  }
}

@media (width >= 1200px) {
  .header-container {
    justify-content: space-between;
    align-items: center;
    width: 1136px;
    margin: 24px 32px;
    display: flex;
  }

  .header-logo {
    letter-spacing: -.54px;
    font-size: 18px;
    line-height: 20px;
  }

  .header_nav {
    align-items: center;
    width: 80%;
    display: flex;
  }

  .header_nav-list {
    gap: 24px;
    display: flex;
  }

  .header_nav-list a {
    color: var(--black);
    letter-spacing: -.28px;
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 16px;
  }

  .header_nav button {
    display: none;
  }

  .header_networks {
    align-items: center;
    gap: 18px;
    width: 66px;
    display: flex;
  }
}

.hero-background {
  background-color: #d3d3d3;
  background-image: linear-gradient(#1110 0%, #1116 100%), url("chocolate-background1.0f03e888.jpg"), none;
  background-position: 80%;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: scroll, scroll, scroll;
  background-origin: padding-box, padding-box, padding-box;
  background-clip: border-box, border-box, border-box;
  border-radius: 45px;
  max-width: calc(100vw - 40px);
  margin: 0 auto 70px;
  padding-top: 395px;
}

.hero-container {
  margin: auto;
  padding: 0 40px 40px;
}

.visually-hidden {
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.hero-text {
  color: var(--white);
  font-family: var(--font);
  letter-spacing: -.6px;
  flex-shrink: 0;
  max-width: 90%;
  margin: 0;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.2;
}

.hero-buttons-all {
  flex-direction: column;
  justify-content: space-between;
  margin-top: 32px;
  display: flex;
}

.hero-buttons {
  gap: 14px;
  display: flex;
}

.hero-button-buy {
  background-color: var(--orange);
  color: var(--white);
  font-family: var(--font);
  cursor: pointer;
  white-space: nowrap;
  border: none;
  border-radius: 100px;
  align-items: flex-start;
  min-width: 120px;
  margin: 0;
  padding: 12px 28px;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-flex;
}

.hero-button-buy:hover, .hero-button-buy:focus {
  background-color: var(--bright-orange);
  color: var(--orange);
}

.hero-button-how {
  color: var(--white);
  font-family: var(--font);
  cursor: pointer;
  border-style: solid;
  border-width: 1px;
  border-color: var(--white);
  white-space: nowrap;
  background: none;
  border-radius: 100px;
  align-items: flex-start;
  gap: 14px;
  min-width: 159px;
  margin: 0;
  padding: 12px 28px;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-flex;
}

.hero-button-how:hover, .hero-button-how:focus {
  color: var(--orange);
  border-color: var(--orange);
}

.hero-button-scroll {
  color: var(--white);
  font-family: var(--font);
  cursor: pointer;
  white-space: nowrap;
  background: none;
  border: none;
  flex-shrink: 0;
  align-items: center;
  gap: 14px;
  margin: 32px auto 0 0;
  padding: 0;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.28571;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.icon-arrow {
  background-color: var(--white);
  fill: var(--orange);
  border-radius: 50%;
  width: 38px;
  height: 38px;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.hero-button-scroll:hover, .hero-button-scroll:focus, .icon-arrow:hover .hero-button-scroll, .icon-arrow:focus .hero-button-scroll {
  color: var(--orange);
}

.hero-button-scroll:hover .icon-arrow, .hero-button-scroll:focus .icon-arrow, .icon-arrow:hover, .icon-arrow:focus {
  background-color: var(--orange);
  fill: var(--white);
}

@media screen and (width >= 375px) and (width <= 480px) {
  .hero-container {
    margin: auto;
    padding: 0 20px 20px;
  }

  .hero-text {
    max-width: 100%;
    margin: auto;
    font-size: 20px;
  }

  .hero-buttons {
    gap: 14px;
    display: flex;
  }

  .hero-button-scroll {
    margin: 32px auto 0 0;
  }

  .icon-arrow {
    width: 28px;
    height: 28px;
  }
}

@media screen and (width >= 768px) {
  .hero-background {
    max-width: calc(100vw - 64px);
