  }

  .hero-button-how {
    width: 183px;
    padding: 14px 40px;
  }

  .hero-button-scroll {
    margin: 0;
  }
}

@media screen and (width >= 1200px) {
  .hero-background {
    max-width: 1136px;
    margin-bottom: 82px;
  }
}

