.flip-card {
  background-color: var(--orange);
  perspective: 1000px;
  border-radius: 15px;
  width: 335px;
  height: 270px;
}

.first-flip-card-color {
  background-color: var(--white);
}

.blue-flip-card-color {
  background-color: var(--grey);
}

.flip-card-inner {
  transform-style: preserve-3d;
  width: 100%;
  height: 100%;
  transition: transform .8s;
  position: relative;
}

.flip-card:hover .flip-card-inner {
  transform: rotateY(180deg);
}

.flip-card-front, .flip-card-back {
  backface-visibility: hidden;
  width: 100%;
  height: 100%;
  position: absolute;
}

.flip-card-front {
  background-color: var(--orange);
  text-align: center;
  border-radius: 15px;
